.addpatients {
	background-color: var(--white);
	padding: 1rem 3rem 3rem 3rem;
	display: flex;
	flex-direction: column;
	overflow-y: hidden;
}
.addpatients__header {
	text-transform: uppercase;
	font-size: 3rem;
	font-weight: 1000;
}
.addpatients__form {
	margin: 2rem 0;
	overflow-y: scroll;
}
.addpatients__back-button {
	margin-left: auto;
}
